import "./App.css";
import { useEffect, useState } from "react";

function App() {
  const [tasks, setTasks] = useState([
    {
      id: 1,
      task_name: "Prvi izazov",
      completed: false,
    },
    {
      id: 2,
      task_name: "Drugi izazov",
      completed: false,
    },
  ]);
  const [update, setUpdate] = useState(false);
  const [name, setName] = useState('')

  useEffect(() => {
    fetch("http://127.0.0.1:8000/api/tasks")
      .then((res) => res.json())
      .then((res) => console.log(res));
  }, [update]);

  const handleChange = (i) => {
    const newTasks = tasks.map((x, index) => {
      if (i === index) {
        x.completed = !x.completed;
        return x;
      } else {
        return x;
      }
    });

    setUpdate(!update);
    setTasks(newTasks);
  };

  const insertInto = () => {
    fetch("http://127.0.0.1:8000/api/tasks/insert", {
      method: "PUT",
      body: JSON.stringify({
        name
      })
    })

    setUpdate(!update)

  };

  const checkAll = () => {
    fetch("http://127.0.0.1:8000/api/tasks/check_all")

    setUpdate(!update);
  };

  const deleteSelected = (i) => {
    fetch("http://127.0.0.1:8000/api/tasks/insert", {
      method: "DELETE",
      body: JSON.stringify({
        i
      })
    })

    setUpdate(!update);
  }

  return (
    <div className="App">
      <p style={{ fontSize: 48, padding: 20 }}>Todo list</p>
      <header className="App-header">
        <div className="outerContainer">
          {tasks.map((x, index) => {
            return (
              <div className="taskContainer">
                <label className="container">
                  <input
                    type="checkbox"
                    checked={x.completed}
                    onChange={() => handleChange(index)}
                  />
                  <span className="checkmark"></span>
                </label>
                <p className='deletable' onClick={() => deleteSelected(x.id)}>
                  {x.task_name}
                </p>
              </div>
            );
          })}
        </div>
        <button onClick={checkAll}>OznaÄ‡i sve todo kao gotove</button> <br />
        <p style={{ fontSize: 24}} >Unesi ime novog taska</p> <br/>
        <input type="text" value={name} onChange={(e) => setName(e.target.value)} /> <br />
        <button onClick={insertInto}>Dodaj novi todo</button>
      </header>
    </div>
  );
}

export default App;
